.App {
  text-align: center;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
}

.App-Inner {
  max-width: 1080px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  background-color: #ffffff;
  min-height: calc(90vh - 20px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 0px;
}

.App-footer {
  font-size: calc(10px + 2vmin);
  background-color:white;
  min-height: 10vh;
  display: flex;
  text-align: right;
  justify-content: center;
  align-items: center;
  color: black;
  margin-top: auto;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "BMJUA";
  src: local("BMJUA"),
      url("./font/BMJUA_ttf.woff") format("woff"),
      url("./font/BMJUA_ttf.ttf") format("truetype"),
      url("./font/BMJUA_otf.otf") format("opentype");
  unicode-range: U+000-5FF, U+AC00-D7AF;
}

h2 {
  font-family: BMJUA;
}

h3 {
  font-family: BMJUA;
}